body {
  overflow: hidden;
  font-size: 1.6rem;
}

.container {
  height: 100vh;
  display: grid;
  grid-template-columns: 260px 1fr;
  grid-template-rows: 1fr auto;
  grid-template-areas: "layers map" "timebar timebar";
  background: #0e2e82;
  color: white;
}

.timebar {
  grid-area: timebar;
}

.layers {
  list-style: none;
  padding: 20px;
  grid-area: layers;
}

.map {
  grid-area: map;
  width: 100%;
  height: 100%;
}

.layers li {
  margin: 30px 10px;
}

.popup {
  color: #0c276c;
}
